import { SHADOWS, TEXT_STYLES } from "@/app/constants";
import React from "react";
import { FaEyeSlash } from "react-icons/fa";

interface InputProps {
  headline: string;
  placeholder: string;
  onChangeText: (text: string) => void;
  isPassInput: boolean;
}

const Input: React.FC<InputProps> = ({
  headline,
  placeholder,
  onChangeText,
  isPassInput,
}) => {
  return (
    <div
      className="flex flex-col items-end gap-3"
      // Apply the shadow style to the main container div
    >
      <label style={TEXT_STYLES.READEX_PRO.SEMIBOLD.H5} className="text-sm">
        {headline}
      </label>
      <div
        className=" border border-gray-300 rounded-md flex items-center px-4 gap-2"
        style={{ boxShadow: SHADOWS.DROP }}
      >
        {isPassInput && <FaEyeSlash className="cursor-pointer" />}
        <input
          style={TEXT_STYLES.READEX_PRO.MEDIUM.H7}
          className=" py-2 text-right"
          onChange={(e) => onChangeText(e.target.value)}
          placeholder={placeholder}
          type={isPassInput ? "password" : "text"}
        />
      </div>
    </div>
  );
};

export default Input;
